<template>
    <div class="seller" ref="seller">
        <div class="seller-content">
            <div class="overview">
                <h1 class="title">{{seller.name}}</h1>
                <div class="desc border-1px">
                    <star :size="36" :score="seller.score" class="star"></star>
                    <span class="text">({{seller.ratingCount}})</span>
                    <span class="text">月售{{seller.sellCount}}单</span>
                </div>
                <ul class="remark">
                    <li class="block">
                        <h2>起送价</h2>
                        <div class="content">
                            <span class="stress">{{seller.minPrice}}</span>元
                        </div>
                    </li>
                    <li class="block">
                        <h2>商家配送</h2>
                        <div class="content">
                            <span class="stress">{{seller.deliveryPrice}}</span>元
                        </div>
                    </li>
                    <li class="block">
                        <h2>平均配送时间</h2>
                        <div class="content">
                            <span class="stress">{{seller.deliveryTime}}</span>分钟
                        </div>
                    </li>
                </ul>
                <div class="favorite">
                    <span
                        class="icon-favorite"
                        :class="{'active':favorite}"
                        @click="toggleFavorite"
                    ></span>
                    <span class="text">{{favoriteText}}</span>
                </div>
            </div>
            <split></split>
            <div class="bulletin">
                <h1 class="title">公告与活动</h1>
                <div class="content-wrapper">
                    <p class="content">{{seller.bulletin}}</p>
                </div>
                <ul v-if="seller.supports" class="supports">
                    <li
                        class="support-item border-1px"
                        v-for="(item,i) in seller.supports"
                        :key="i"
                    >
                        <span class="icon" :class="classMap[seller.supports[i].type]"></span>
                        <span class="text">{{seller.supports[i].description}}</span>
                    </li>
                </ul>
            </div>
            <split></split>
            <div class="pics">
                <h1 class="title">商家实景</h1>
                <div class="pic-wrapper" ref="picWrapper">
                    <ul class="pic-list" ref="picList">
                        <li class="pic-item" v-for="(pic,i) in seller.pics" :key="i">
                            <img :src="pic" width="120" height="90" />
                        </li>
                    </ul>
                </div>
            </div>
            <split></split>
            <div class="info">
                <h1 class="title border-1px">商家信息</h1>
                <ul>
                    <li
                        class="info-item border-1px"
                        v-for="(info,i) in seller.infos"
                        :key="i"
                    >{{info}}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import star from "../star/star";
import split from "../split/split";
import BScroll from "better-scroll";
export default {
    props: ["seller"],
    components: {
        star,
        split
    },
    data() {
        return {
            count: 0,
            classMap: [
                "decrease",
                "discount",
                "special",
                "invoice",
                "guarantee"
            ],
            favorite: false
        };
    },
    created() {
        if(localStorage.getItem(this.seller.id)){
            this.favorite = localStorage.getItem(this.seller.id)==='1'?true: false;
        }
        this.$nextTick(() => {
            this.scroll = new BScroll(this.$refs.seller, {
                click: true
            });
        });
    },
    watch: {
        //当seller发生变化时，刷新或者为这个组件添加 better-scroll
        //解决当seller ajax异步获取时，不能第一时间传送过来导致better-scroll计算错误不能滚动的情况
        seller() {
            if (!this.scroll) {
                this.scroll = new BScroll(this.$refs.seller, {
                    click: true
                });
            } else {
                this.scroll.refresh();
            }
            console.log("seller发生变化了");
        }
    },
    mounted() {
        if (this.seller.pics) {
            let picWidth = 120;
            let margin = 6;
            let width = (picWidth + margin) * this.seller.pics.length - margin;
            this.$refs.picList.style.width = width + "px";
            this.$nextTick(() => {
                this.picScroll = new BScroll(this.$refs.picWrapper, {
                    scrollX: true,
                    eventPassthrough: "vertical" //横行滚动，忽略竖直滚动
                });
            });
        }
    },
    computed: {
        favoriteText() {
            return this.favorite ? "已收藏" : "收藏";
        }
    },
    methods: {
        toggleFavorite(event){
            if(!event._constructed){
                return
            }
            this.favorite = !this.favorite;
            let count = this.favorite===true?'1':'0';
            window.localStorage.setItem(this.seller.id,count);
        }
    }
};
</script>

