<template>
    <div class="cartcontrol">
        <transition name="zs">
            <div class="cart-decrease" @click="decreaseCart($event)" v-show="food.count>0">
                <!-- < class="icon-remove_circle_outline inner" ></> -->
                <span class="icon-remove_circle_outline in"></span>
            </div>
        </transition>
        <!-- ！！！！！！！！！！！！！！！！！！！！！！！！！！！ -->
        <!-- 为什么要把这个dom也套上一个transition是因为，如果不给它transition那么它会突然消失或者出现 -->
        <!-- transition 属性不会继承 -->
        <!-- 只有transition 效果完成后，dom在页面中占据的位置才会消失 -->
        <!-- 如果这个元素突然消失，那么它周围的元素的位置也会立即刷新改变，就会让 decrease 按钮的动画效果不自然（离开的时候会突然闪一下） -->
        <!-- 可以设置动画的元素绝对定位，这样位置就不会被突然改变了，也就不会突然闪一下了 -->
        <!-- <transition> -->
        <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
        <!-- </transition> -->

        <div class="cart-add" @click="addCart($event)">
            <span class="icon-add_circle"></span>
        </div>
    </div>
</template>

<script>
import Vue from "vue";

export default {
    props: ["food"],
    created() {
        // console.log(this.food);
    },
    methods: {
        addCart(event) {
            if (!event._constructed) {
                return;
            }
            if (!this.food.count) {
                // this.food.count = 1  Vue检测不到这个动态的变化，不会重新渲染dom
                Vue.set(this.food, "count", 1);
            } else {
                this.food.count++;
            }
            console.log('event.target')
            console.log(event.target)

            this.$emit("ballDrop", event.target);
        },
        decreaseCart(event) {
            if (!event._constructed) {
                return;
            }
            if (this.food.count > 0) {
                this.food.count--;
            }
        },
    },
};
</script>

<style lang="scss" scoped>
.cartcontrol {
    position: absolute;
    right: 0;
    bottom: 10px;
    font-size: 0;
    .cart-add {
        display: inline-block;
        padding: 6px;
        line-height: 24px;
        font-size: 24px;
        color: rgb(0, 160, 220);
    }
    .cart-decrease {
        // position: absolute;
        right: 46px;
        display: inline-block;
        padding: 6px;
        transition: .5s;
        font-size: 2px;
        //减号出现与离开的动画
        //active的样式要写在前面，不然没有动画效果
        &.zs-enter-active,
        &.zs-leave-active {
            opacity: 1;
            // transform: translate(0);
            .in {
                // transform: rotate(0);
                transform: translate(0) rotate(0);
            }
        }
        &.zs-enter,
        &.zs-leave-to {
            opacity: 0;
            // transform: translate(30px);
            .in {
                // transform: rotate(180deg);
                transform: translate(30px) rotate(180deg);
            }
        }
    }
    .icon-remove_circle_outline {
        // 内联元素设置 rotate 属性没有效果，所以先改变一下display属性
        display: inline-block;
        transition: .5s;
        line-height: 24px;
        font-size: 24px;
        color: rgb(0, 160, 220);
    }
    .cart-count {
        display: inline-block;
        vertical-align: top;
        width: 12px;
        padding-top: 6px;
        line-height: 24px;
        text-align: center;
        font-size: 10px;
        color: rgb(147, 153, 159);
    }
}
</style>
