<template>
    <transition>
        <div class="food-wrapper" ref="food" v-show="showFood">
            <div class="food">
                <div class="image-header">
                    <img :src="food.image" alt />
                    <div class="back" @click="hide">
                        <span class="icon-arrow_lift"></span>
                    </div>
                </div>
                <div class="content">
                    <h1 class="title">{{food.name}}</h1>
                    <div class="detail">
                        <span class="sell-count">月售{{food.sellCount}}份</span>
                        <span class="rating">好评率{{food.rating}}%</span>
                    </div>
                    <div class="price">
                        <span class="now">¥{{food.price}}</span>
                        <span class="old" v-show="food.oldPrice">¥{{food.oldPrice}}</span>
                    </div>
                    <div class="cartcontrol-wrapper">
                        <cartcontrol :food="food" @ballDrop="ballDropMiddle"></cartcontrol>
                    </div>
                    <div
                        @click="addFirst($event)"
                        class="buy"
                        v-show="!food.count || food.count===0"
                    >加入购物车</div>
                    <!-- 因为点击加入购物车时，按钮消失了，这时候如果要给它加一个小球动画，但是不知道位置了，所以事先给它放一个兄弟 ball 用来记录它的位置，到时候直接用 ball 的位置就行 -->
                    <div class="ball" ref="ball"></div>
                </div>
                <split v-show="food.info"></split>
                <div class="info" v-show="food.info">
                    <h1 class="title">商品信息</h1>
                    <p class="text">{{food.info}}</p>
                </div>
                <split></split>
                <div class="rating">
                    <h1 class="title">商品评价</h1>
                    <ratingselect
                        :selectType="selectType"
                        :onlyContent="onlyContent"
                        :desc="desc"
                        :ratings="food.ratings"
                        @changeType="changeType"
                        @changeContentOnly="changeContentOnly"
                        @bsRefresh="bsRefresh"
                        ref="ratingselect"
                    ></ratingselect>
                    <div class="rating-wrapper">
                        <ul v-show="food.ratings && food.ratings.length">
                            <li
                                v-show="isShow(rating.rateType,rating.text)"
                                v-for="(rating,i) in food.ratings"
                                :key="i"
                                class="rating-item"
                            >
                                <div class="user">
                                    <span class="name">{{rating.username}}</span>
                                    <img alt width="12" height="12" :src="rating.avatar" />
                                </div>
                                <div class="time">{{rating.rateTime | formatDate}}</div>
                                <p class="text">
                                    <span
                                        :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"
                                    ></span>
                                    {{rating.text}}
                                </p>
                            </li>
                        </ul>
                        <div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import BScroll from "better-scroll";
import Vue from "vue";
import cartcontrol from "../cartcontrol/cartcontrol";
import split from "../split/split";
import ratingselect from "../ratingslect/ratingslect";
import {formatDate} from '../../common/js/date';

const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;
export default {
    props: ["food"],
    data() {
        return {
            showFood: false,
            selectType: ALL,
            onlyContent: true,
            desc: {
                all: "全部",
                positive: "推荐",
                negative: "吐槽"
            }
        };
    },
    methods: {
        show() {
            this.showFood = true;
            this.$refs.ratingselect.ratingsRealShow(); //打开good 组件时，立即把ratings 赋值给 占位元素 ratingsCopy;
            this.selectType = ALL;
            this.onlyContent = true;
            this.$nextTick(() => {
                if (!this.scroll) {
                    this.scroll = new BScroll(this.$refs.food, {
                        click: true
                    });
                } else {
                    this.scroll.refresh();
                }
            });
        },
        hide() {
            this.showFood = false;
        },
        addFirst(event) {
            if (!event._constructed) {
                return;
            }
            Vue.set(this.food, "count", 1);
            this.$emit("ballDrop", this.$refs.ball);
        },
        ballDropMiddle(target) {
            //给加减按钮创建这个方法是因为，goods里面的方法不能传两级，所以中间自己建一个方法用来中转
            this.$emit("ballDrop", target);
        },
        changeType(type) {
            this.selectType = type;
        },
        changeContentOnly() {
            this.onlyContent = !this.onlyContent;
        },
        isShow(type, text) {
            //判断该评论是否应该显示
            if (this.onlyContent && !text) {
                return false;
            }
            if (this.selectType === ALL) {
                return true;
            } else {
                return type === this.selectType;
            }
        },
        bsRefresh() {
            //当改变显示评论的方式时重新计算 better-scroll的高度
            this.$nextTick(() => {
                this.scroll.refresh();
            });
        }
    },
    components: {
        cartcontrol,
        split,
        ratingselect
    },
    filters: {
        formatDate(time){//格式化时间
            let date = new Date(time);
            return formatDate(date,'yyyy-MM-dd hh:mm');
        }
    }
};
</script>

