<template>
    <div class="ratingselect">
        <div class="rating-type border-1px">
            <span class="block all" @click="select(2,$event)" :class="{'active':selectType===2}">
                {{descReal.all}}
                <span class="count">{{ratingsCopy.length}}</span>
            </span>
            <span
                class="block positive"
                @click="select(0,$event)"
                :class="{'active':selectType===0}"
            >
                {{descReal.positive}}
                <span class="count">{{positives.length}}</span>
            </span>
            <span
                class="block negative"
                @click="select(1,$event)"
                :class="{'active':selectType===1}"
            >
                {{descReal.negative}}
                <span class="count">{{negatives.length}}</span>
            </span>
        </div>
        <div class="switch" @click="toggleContent" :class="{'on': onlyContent}">
            <span class="icon-check_circle"></span>
            <span class="text">只看有内容的评价</span>
        </div>
    </div>
</template>

<script>
const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;
export default {
    props: ["ratings", "selectType", "onlyContent", "desc"],
    data() {
        return {
            ratingsCopy: [] //在 good组件打开前，充当ratings的占位元素，以为good组件不打开，它下面的组件也会创建，此时为避免访问还没传送下来的ratings 的 length 产生报错
        };
    },
    computed: {
        descReal() {
            //当不传desc时给它一个默认值
            try {
                if (typeof this.desc === "undefined") {
                    return {
                        all: "全部",
                        positive: "满意",
                        negative: "不满意"
                    };
                } else {
                    return this.desc;
                }
            } catch (e) {
                return {
                    all: "全部",
                    positive: "满意",
                    negative: "不满意"
                };
            }
        },
        positives() {
            return this.ratingsCopy.filter(rating => {
                return rating.rateType === POSITIVE;
            });
        },
        negatives() {
            return this.ratingsCopy.filter(rating => {
                return rating.rateType === NEGATIVE;
            });
        }
    },
    methods: {
        select(type, event) {
            if (!event._constructed) {
                return;
            }
            this.$emit("changeType", type);
            this.$emit("bsRefresh");
        },
        toggleContent(event) {
            if (!event._constructed) {
                return;
            }
            this.$emit("changeContentOnly");
            this.$emit("bsRefresh");
        },
        ratingsRealShow() {
            //真正的 ratings 传送下来时，把它赋值给 ratingsCopy
            this.$nextTick(() => {//不放在这个函数里面的话，this.ratings 一开始还是undefined.
                this.ratingsCopy = this.ratings;
            });
        }
    }
};
</script>

