            border-radius: 1px;
            color: rgb(77, 85, 93);
            font-size: 12px;
            line-height: 16px;
            &.active {
                color: #fff;
            }
            .count {
                font-size: 8px;
                margin-left: 2px;
            }
            &.all {
                background-color: rgb(0, 160, 220, 0.2);
                &.active {
                    background-color: rgba(0, 160, 220);
                }
            }
            &.positive {
                background-color: rgba(0, 160, 220, 0.2);
                &.active {
                    background-color: rgb(0, 160, 220);
                }
            }
            &.negative {
                background-color: rgba(77, 85, 93, 0.2);
                &.active {
                    background-color: rgb(77, 85, 93);
                }
            }
        }
    }
    .switch {
        padding: 12px 18px;
        line-height: 24px;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        color: rgb(147, 153, 159);
        font-size: 0;
        &.on {
            .icon-check_circle {
                color: #00c850;
            }
        }
        .icon-check_circle {
            display: inline-block;
            vertical-align: top;
