export function formatDate(date, format){//格式化时间函数
    // console.log(date);
    if(/(y+)/.test(format)){
        format = format.replace(RegExp.$1,(date.getFullYear()+'').substr(4-RegExp.$1.length));
    }
    let o = {
        'M+': date.getMonth()+1,
        'd+': date.getDate(),
        'h+': date.getHours(),
        'm+': date.getSeconds()
    }
    for ( let k in o ){
        if(new RegExp(`(${k})`).test(format)){
            let str = o[k]+'';
            format = format.replace(RegExp.$1,(RegExp.$1.length===1)?str.padStart(2,'0'):str);
        }
        //padStart
        /*
        function padStart(str){
            return ('00'+str).substr(str.length);
        }
        */
    }
    return format;
};
