<template>
    <div class="goods">
        <div class="menu-wrapper" ref="menuWrapper">
            <ul>
                <!--   :class="{'active': (flag===true?currentIndexCopy:currentIndex) ===i}"-->
                
                <li
                    v-for="(item, i) in goods"
                    :key="i"
                    class="menu-item"
                    :class="{'active': (flag===true?currentIndexCopy:currentIndex) ===i}"
                    @click="selectMenu(i, $event)"
                >
                    <span class="text border-1px">
                        <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>
                        {{item.name}}
                    </span>
                </li>
            </ul>
        </div>
        <div class="foods-wrapper" ref="foodsWrapper">
            <ul>
                <!-- food-list-hook的类名只是为了被js选择 -->
                <li v-for="(item,i) in goods" :key="i" class="foods-list food-list-hook">
                    <h1 class="title">{{item.name}}</h1>
                    <ul>
                        <li
                            v-for="(food,i) in item.foods"
                            :key="i"
                            class="food-item border-1px"
                        >
                            <div class="icon"  @click="selectFoodFun(food,$event)">
                                <img :src="food.icon" alt style="width:57px;height:57px" />
                            </div>
                            <div class="content">
                                <h2 class="name">{{food.name}}</h2>
                                <p class="desc">{{food.description}}</p>
                                <div class="extra">
                                    <span class="count">月售{{food.sellCount}}份</span>
                                    <span>好评率{{food.rating}}%</span>
                                </div>
                                <div class="price">
                                    <span class="now">¥{{food.price}}</span>
                                    <span class="old" v-show="food.oldPrice">¥{{food.oldPrice}}</span>
                                </div>
                                <div class="cartcontrol-wrapper">
                                    <cartcontrol :food="food" @ballDrop="_drop"></cartcontrol>
                                </div>
                            </div>
                        </li>
                    </ul>
                </li>
            </ul>
        </div>
        <shopcart
            :deliveryPrice="seller.deliveryPrice"
            :minPrice="seller.minPrice"
            :selectFoods="selectFoods"
            :ballDrop="ballDrop"
            ref="shopcart"
        ></shopcart>
        <food :food="selectFood" ref="food" @ballDrop="_drop"></food>
    </div>
</template>

<script>
import data from "../../../data.json";
//引入better-scroll
import BScroll from "better-scroll";
import shopcart from "../shopcart/shopcart";
import cartcontrol from "../cartcontrol/cartcontrol";
import food from "../food/food";

const ERR_OK = 0;
export default {
    data() {
        return {
            goods: [],
            listHeight: [], //存放不同分区foods的容器高度,
            scrollY: 0, //用来记录foods区块滚动的高度
            currentIndexCopy: 0, //点击菜单时候直接激活高亮
            flag: false, //true表示使用copyIndexCopy来进行高亮，false表示用currentIndex来进行高亮
            timer: null,
            ballMsg: null, //存放点击购物控件的信息
            selectFood: 'msg'
        };
    },
    props: ["seller"],
    created() {
        this.classMap = [
            "decrease",
            "discount",
            "special",
            "invoice",
            "guarantee"
        ];
        this.goods = data.goods;
        // console.log(this.goods);
        this.$nextTick(() => {
            //操作dom元素， 最好放到nextTick 里面，因为拿到更新后的dom，是在nextTick后拿到
            this._initScroll(); //运用better-scroll到dom元素上
            this._calculateHeight();
        });
    },
    computed: {
        currentIndex() {
            //得到 menu 需要激活的 item的索引
            for (let i = 0; i < this.listHeight.length; i++) {
                let height1 = this.listHeight[i];
                let height2 = this.listHeight[i + 1];
                if (
                    !height2 ||
                    (this.scrollY >= height1 && this.scrollY < height2)
                ) {
                    return i;
                }
            }
            return 0;
        },
        selectFoods() {
            let foods = [];
            this.goods.forEach(good => {
                good.foods.forEach(food => {
                    if (food.count > 0) {
                        foods.push(food);
                    }
                });
            });
            return foods;
        },
        ballDrop() {
            return this.ballMsg;
        }
    },
    methods: {
        selectMenu(index, event) {
            this.flag = true;
            if (!event._constructed) {
                //原生的点击事件没有 _constructed 这个属性
                return;
            }
            clearTimeout(this.timer);
            let foodList = this.$refs.foodsWrapper.getElementsByClassName(
                "food-list-hook"
            );
            let el = foodList[index];
            this.foodsScroll.scrollToElement(el, 500); //主动滚动到哪个子元素处，和多久滚动过去

            this.currentIndexCopy = index; //点击的时候立即高亮对应的menu
            this.timer = setTimeout(() => {
                //点击菜单滑动的效果完成之后才把flag变为false，也就是在点击菜单滑动效果期间只使用currentIndexCopy进行高亮
                this.flag = false;
            }, 500);
        },
        _initScroll() {
            this.menuScroll = new BScroll(this.$refs.menuWrapper, {
                click: true //可以触发点击事件
            });
            this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
                probeType: 3, //表示实时得到滚动高度
                click: true
            });

            this.foodsScroll.on("scroll", pos => {
                //pos.y是一个负值
                this.scrollY = Math.abs(Math.round(pos.y));
            });
        },
        _calculateHeight() {
            let foodList = this.$refs.foodsWrapper.getElementsByClassName(
                "food-list-hook"
            );
            let height = 0;
            this.listHeight.push(height);
            for (let i = 0; i < foodList.length; i++) {
                height += foodList[i].clientHeight;
                this.listHeight.push(height);
            }
        },
        _drop(target) {
            this.ballMsg = target; //第一种传值方式
            this.$nextTick(() => {
                //防止同时产生两种动画时（减号动画，小球动画）的卡顿，体验更优化
                this.$refs.shopcart.ballMove(target); //第二种传值方式
            });
        },
        selectFoodFun(food, event) { //点击具体的food时，把这个food 传给food详情组件
            if (!event._constructed) {
                return;
            }
            console.log(food)
            this.selectFood = food;
            this.$refs.food.show();
        }
    },
    components: {
        shopcart,
        cartcontrol,
        food
    }
};
</script>

