<template>
    <div class="ratings" ref="ratings">
        <div class="ratings-content">
            <div class="overview">
                <div class="overview-left">
                    <h1 class="score">{{seller.score}}</h1>
                    <div class="title">综合评分</div>
                    <div class="rank">高于周边商家{{seller.rankRate}}%</div>
                </div>
                <div class="overview-right">
                    <div class="score-wrapper">
                        <span class="title">服务态度</span>
                        <star class="star" :size="36" :score="seller.serviceScore"></star>
                        <span class="score">{{seller.serviceScore}}</span>
                    </div>
                    <div class="score-wrapper">
                        <span class="title">商品评分</span>
                        <star class="star" :size="36" :score="seller.foodScore"></star>
                        <span class="score">{{seller.foodScore}}</span>
                    </div>
                    <div class="delivery-wrapper">
                        <span class="title">送达时间</span>
                        <span class="delivery">{{seller.deliveryTime}}分钟</span>
                    </div>
                </div>
            </div>
            <split></split>
            <ratingslect
                :ratings="ratings"
                :onlyContent="onlyContent"
                :selectType="selectType"
                @changeType="changeType"
                @changeContentOnly="changeContentOnly"
                ref="ratingselect"
            ></ratingslect>
            <div class="rating-wrapper">
                <ul>
                    <li
                        v-for="(rating,i) in ratings"
                        :key="i"
                        class="rating-item"
                        v-show="isShow(rating.rateType,rating.text)"
                    >
                        <div class="avatar">
                            <img :src="rating.avatar" alt width="28px" height="28px" />
                        </div>
                        <div class="content">
                            <h1 class="name">{{rating.username}}</h1>
                            <div class="star-wrapper">
                                <star :size="24" :score="rating.score" class="star"></star>
                                <span
                                    class="delivery"
                                    v-show="rating.deliveryTime"
                                >{{rating.deliveryTime}}分钟送达</span>
                            </div>
                            <p class="text">{{rating.text}}</p>
                            <div
                                class="recommend"
                                v-show="rating.recommend && rating.recommend.length"
                            >
                                <span
                                    :class="rating.rateType == 0 ? 'icon-thumb_up':'icon-thumb_down'"
                                ></span>
                                <span
                                    class="item"
                                    v-for="(recommend,i) in rating.recommend"
                                    :key="i"
                                >{{recommend}}</span>
                            </div>
                            <span
                                v-show="!(rating.recommend && rating.recommend.length)"
                                :class="rating.rateType == 0 ? 'icon-thumb_up':'icon-thumb_down'"
                            ></span>
                            <div class="time">{{rating.rateTime | formatDate}}</div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import { formatDate } from "../../common/js/date";
import BScroll from "better-scroll";
import star from "../star/star";
import split from "../split/split";
import ratingslect from "../ratingslect/ratingslect";

const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;
import data from "../../../data.json";
export default {
    props: ["seller"],
    components: {
        star,
        split,
        ratingslect
    },
    data() {
        return {
            ratings: [],
            selectType: ALL,
            onlyContent: true
        };
    },
    created() {
        this.ratings = data.ratings;
    },
    mounted() {
        this.$refs.ratingselect.ratingsRealShow(); //打开good 组件时，立即把ratings 赋值给 占位元素 ratingsCopy;
        this.bscroll = new BScroll(this.$refs.ratings, {
            click: true
        });
    },
    methods: {
        changeType(type) {
            this.selectType = type;
        },
        changeContentOnly() {
            this.onlyContent = !this.onlyContent;
        },
        isShow(type, text) {
            //判断该评论是否应该显示
            if (this.onlyContent && !text) {
                return false;
            }
            if (this.selectType === ALL) {
                return true;
            } else {
                return type === this.selectType;
            }
        }
    },
    filters: {
        formatDate(time) {
            let date = new Date(time);
            return formatDate(date, "yyyy-MM-dd hh:mm");
        }
    }
};
</script>

