<template>
    <div>
        <div class="shopcart">
            <div class="content-left" @click="fold">
                <div class="logo-wrapper">
                    <div class="logo" :class="{'highlight': totalCount>0}">
                        <span class="icon-shopping_cart" :class="{'highlight': totalCount>0}"></span>
                    </div>
                    <div class="num" v-show="totalCount>0">{{totalCount}}</div>
                </div>
                <div class="price" :class="{'highlight': totalCount>0}">¥{{totalPrice}}元</div>
                <div class="desc">另需配送费¥{{deliveryPrice}}元</div>
            </div>
            <div class="content-right" @click="pay">
                <div class="pay" :class="payClass">{{payDesc}}</div>
            </div>
            <div class="ball-container">
                <transition-group
                    name="drop"
                    @before-enter="beforeEnter"
                    @enter="enter"
                    @after-enter="afterEnter"
                >
                    <div v-for="(ball,i) in balls" :key="i" v-show="ball.show" class="ball">
                        <div class="inner inner-hook"></div>
                    </div>
                </transition-group>
            </div>
            <!-- 购物车详情页面 -->
            <transition name="fold">
                <div class="shopcart-list" v-show="listShowCopy">
                    <div class="list-header">
                        <h1 class="title">购物车</h1>
                        <span class="empty" @click="empty">清空</span>
                    </div>
                    <div class="list-content" ref="listWrapper">
                        <ul>
                            <li class="food" v-for="(food,i) in selectFoods" :key="i">
                                <span class="name">{{food.name}}</span>
                                <div class="price">
                                    <span>¥{{food.price*food.count}}</span>
                                </div>
                                <div class="cartcontrol-wrapper" @click="isRefresh">
                                    <cartcontrol :food="food"></cartcontrol>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </transition>
        </div>
        <transition name="mask">
            <div class="list-mask" v-show="listShowCopy" @click="fold"></div>
        </transition>
    </div>
</template>

<script>
import cartcontrol from "../cartcontrol/cartcontrol";
import betterScroll from "better-scroll";
export default {
    //selectFoods用来传递选择了哪些食物的信息
    props: ["deliveryPrice", "minPrice", "selectFoods", "ballDrop"],
    data() {
        return {
            balls: [
                { show: false },
                { show: false },
                { show: false },
                { show: false },
                { show: false },
                { show: false },
                { show: false },
                { show: false },
                { show: false },
                { show: false },
                { show: false },
                { show: false },
                { show: false },
                { show: false },
                { show: false }
            ],
            dropBall: [], //用来记录多次点击小球的先后顺序，以便动画的时候按顺序执行相应的动画
            dropBallCopy: [], //在enter动画后，依次把小球的 display 置为none;
            listShow: false
        };
    },
    computed: {
        totalPrice() {
            let total = 0;
            this.selectFoods.forEach(food => {
                total += food.price * food.count;
            });
            return total;
        },
        totalCount() {
            let count = 0;
            this.selectFoods.forEach(food => {
                count += food.count;
            });
            return count;
        },
        payDesc() {
            if (this.totalPrice === 0) {
                return `¥${this.minPrice}元起送`;
            } else if (this.totalPrice < this.minPrice) {
                return `还差¥${this.minPrice - this.totalPrice}元起送`;
            } else {
                return "去结算";
            }
        },
        payClass() {
            if (this.totalPrice < this.minPrice) {
                return "not-enough";
            } else {
                return "enough";
            }
        },
        listShowCopy() {
            if (this.listShow && this.totalCount > 0) {
                return true;
            } else {
                this.listShow = false;
                return false;
            }
        }
    },
    methods: {
        ballMove(target) {
            // console.log(target);
            for (let i = 0; i < this.balls.length; i++) {
                let ball = this.balls[i];
                if (!ball.show) {
                    //找到一个空闲的小球
                    ball.show = true;
                    ball.el = target;
                    this.dropBall.push(ball);
                    return;
                }
            }
        },
        beforeEnter(el) {
            console.log('el')
            console.log(el)
            let ball = this.dropBall.shift(); //按顺序拿到小球
            this.dropBallCopy.push(ball);
            let rect = ball.el.getBoundingClientRect();
            console.log('rect')
            console.log(rect)
            let x = rect.left - 42;
            let y = -(window.innerHeight - rect.top - 42);
            el.style.transform = `translate3d(0,${y}px,0)`;
            let inner = el.getElementsByClassName("inner-hook")[0];
            inner.style.transform = `translate3d(${x}px,0,0)`;
        },
        enter(el) {
            el.offsetWidth;
            el.style.transform = `translate3d(0,0,0)`;
            let inner = el.getElementsByClassName("inner-hook")[0];
            inner.style.transform = `translate3d(0,0,0)`;
        },
        afterEnter(el) {
            this.dropBallCopy.shift().show = false;
        },
        fold() {
            if (this.totalCount) {
                this.listShow = !this.listShow;
                this.$nextTick(() => {
                    if (!this.scroll) {
                        this.scroll = new betterScroll(this.$refs.listWrapper, {
                            click: true
                        });
                    } else {
                        this.scroll.refresh(); //这样不用重复的创建betterScroll 对象
                    }
                });
            } else if (this.listShow === true) {
                this.listShow = false;
            }
        },
        empty() {
            //清空购物车
            this.selectFoods.forEach(food => {
                food.count = 0;
            });
        },
        pay() {
            if (this.totalPrice < this.minPrice) {
                return;
            }
            alert(`需支付¥${this.totalPrice}元`);
        },
        isRefresh(event) {
            //判断当在购物车里删除商品时是否要重新刷新betterScroll的高度
            if (!event._constructed) {
                return;
            }
            console.log("123");
            for (let i = 0; i < this.selectFoods.length; i++) {
                if (this.selectFoods[i].count === 0) {
                    this.scroll.refresh();
                    return;
                }
            }
        }
    },
    components: {
        cartcontrol
    }
};
</script>

